export default {
  data () {
    return {
      goodsList: []
    }
  },
  onShow () {
    // 每次发请求前，都重置goodsList
    this.goodsList = []
    this.queryGoodsList()
    wx.setTabBarBadge({
      index: 2,
      text: Object.keys(this.$store.getters.getCart).length + ''
    })
  },
  onHide () {
    this.$store.commit('updateCart', this.goodsList)
  },
  computed: {
    isAll: {
      get () {
        return this.goodsList.every(v => {
          return v.checked
        })
      },
      set (status) {
        // 如果想真正改变计算属性，需要改变计算属性的依赖
        this.goodsList.forEach(v => {
          v.checked = status
        })
      }
    },
    totalNum () {
      // 所有被选中商品的数量之和
      return this.goodsList.reduce((sum, v) => {
        return sum + (v.checked ? v.num : 0)
      }, 0)
    },
    totalPrice () {
      // 所有被选中商品的数量*价格的和
      return this.goodsList.reduce((sum, v) => {
        return sum + (v.checked ? v.num * v.goods_price : 0)
      }, 0)
    }
  },
  methods: {
    // 跳转支付页面
    toPay () {
      // 如果商品为0，提示
      if (this.totalNum === 0) {
        wx.showToast({
          title: '请选择要购买的商品',
          icon: 'none'
        })
        return
      }

      let token = wx.getStorageSync('token')
      if (!token) {
        // 在模拟器上，不用return;在手机上，需要return
        wx.navigateTo({ url: '/pages/login/main' })
        return
      }

      // 如果用户未登录，跳转登录
      wx.navigateTo({ url: '/pages/pay/main' })
    },
    async queryGoodsList () {
      let cart = this.$store.getters.getCart
      let ids = Object.keys(cart).join(',')
      // 如果ids为空，不必发请求
      if (!ids) {
        return
      }
      let goodsList = await this.$request({
        url: '/api/public/v1/goods/goodslist?goods_ids=' + ids
      })
      // 合并数据源
      goodsList.forEach(v => {
        v.num = cart[v.goods_id].num
        v.checked = cart[v.goods_id].checked
      })

      this.goodsList = goodsList
    }
  }

}
